@import "~normalize.css";
@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,300;0,400;0,500;0,700;1,500;1,700;1,900&display=swap');

a,
a:visited,
a:hover,
a:active {
  color: inherit;
}

//html {
//  scroll-behavior: smooth;
//}

html,
body,
body > div,
body > div > div {
  min-height: 100%;
}

*,
body {
  box-sizing: border-box;
}
h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

ul {
  margin: 0;
  padding: 0;
}

li {
  list-style-type: none;
}

a:hover,
a:visited,
a:link,
a:active {
  text-decoration: none;
}

body {
  background-color: #e5e5e5;
  color: #282d33;
  font-family: "Montserrat";
  font-weight: normal;
  font-size: 18px;
}

textarea:focus,
input:focus,
select:focus {
  outline: none;
}